.form__group {
  position: relative;
  /* display: flex; */
  /* width: 30%; */
}

.form__field {
  font-family: inherit;
  width: 300px;
  border: 0;
  border-bottom: 2px solid #b171d9;
  outline: 0;
  font-size: 1.3rem;
  color: #fff;
  padding: 7px 0;
  background: transparent;
  transition: border-color 0.2s;
}

.form__field::-webkit-input-placeholder {
  color: #ffffff;
  opacity: 0.5;
}

.form__field:-ms-input-placeholder {
  color: #ffffff;
  opacity: 0.5;
}

.form__field::placeholder {
  color: #ffffff;
  opacity: 0.5;
}

.form__field:-ms-input-placeholder ~ .form__label {
  font-size: 1.3rem;
  cursor: text;
  top: 20px;
}

.form__field:placeholder-shown ~ .form__label {
  font-size: 1.3rem;
  cursor: text;
  top: 20px;
}

.form__label {
  position: absolute;
  top: 0;
  display: block;
  transition: 0.2s;
  font-size: 1rem;
  color: #9b9b9b;
}

.form__field:focus {
  /* padding-bottom: 6px; */
  font-weight: 600;
  border-width: 3px;
  /* border-image: linear-gradient(to right, #11998e, #38ef7d); */
  /* border-image-slice: 1; */
}

.form__field:focus ~ .form__label {
  position: absolute;
  top: 0;
  display: block;
  transition: 0.2s;
  font-size: 1rem;
  color: #11998e;
  font-weight: 700;
}

/* reset input */
.form__field:required,
.form__field:invalid {
  box-shadow: none;
}

